.container{
  margin: 0vw 4vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}


@media screen and (max-width: 8000px) {
   img{ 
    width: 21%;
    height: auto;
    padding: 0 90px;
    padding-top: 90px;
    padding-bottom: 100px;
 }
}

@media screen and (max-width: 2000px) {
   img{ 
    width: 25%;
    height: auto;
    padding: 0 50px;
    padding-top: 90px;
    padding-bottom: 40px;
 }
}

@media screen and (max-width: 1024px) {
   img{ 
    width: 44%;
    height: auto;
    padding: 20px 20px;
 }
}


@media screen and (max-width: 854px) {
   img{ 
    width: 90%;
    height: 50%;
    padding: 20px 0;
 }
}



