.navbar {
  margin: 0vw;
  padding: 0vw;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  margin-top:0.2vw;
  padding-right: 1.5vw;
  width: 100%;
}




h3 {
  float: left;
  display: block;
  color: white;
  text-align: right;
  padding: 0vw 0.5vw 0vw 0.5vw;
  text-decoration: none;
  font-size: 15px;
}

