.contactdiv {
	text-align: center;
}



@media screen and (max-width: 8000px) {
.contacttitle{
	font-size: 250px;
	margin-top: 2em;
	padding-bottom: 1em;
	text-align: center;
	color: white;
	text-shadow: 0.15em 0.2em #000;
}

.contactlogo{
	width:3%;
	height: auto;
	margin:0;
	padding: 0 3%;
}

.linkto{
	display: flex;
	justify-content: center;
	color: #483D8B;
	margin-right: 5%;
}

h2{
	font-size: 100px;

}
}

@media screen and (max-width: 2000px) {
.contacttitle{
	font-size: 60px;
	margin-top: 1.5em;
	padding-bottom: 0.5em;
	text-align: center;
	color: white;
	text-shadow: 0.15em 0.2em #000;
}

.contactlogo{
	width:3%;
	height: auto;
	margin:0;
	padding: 0 3%;
}

.linkto{
	display: flex;
	justify-content: center;
	color: #483D8B;
}

h2{
	font-size: 30px;

}
}

@media screen and (max-width: 1024px) {
.contacttitle{
	font-size: 70px;
	margin-top: 3em;
	padding-bottom: 1em;
	text-align: center;
	color: white;
	text-shadow: 0.15em 0.2em #000;
}

.contactlogo{
	width:3%;
	height: auto;
	margin:0;
	padding: 0 3%;
}

.linkto{
	display: flex;
	justify-content: center;
	color: #483D8B;
}

h2{
	font-size: 40px;

}
}




@media screen and (max-width: 854px) {
.contacttitle{
	font-size: 30px;
	margin-top: 3em;
	padding-bottom: 1em;
	text-align: center;
	color: white;
	text-shadow: 0.15em 0.2em #000;
}

.contactlogo{
	width:3%;
	height: 3%;
	margin:0;
	padding: 0 10px;
}

.linkto{
	display: flex;
	justify-content: center;
	color: #483D8B;
}

h2{
	font-size: 20px;
	margin-top: 0;

}
}