.bio {
	margin: 5% 10%;
	display: flex;
	color: white;
	justify-content: center;
}

.bioimage{
	width: 50%;
	height: 30%;
	border-radius: 10px;
	padding-top: 0;
	
}

.biotext{
	margin:0;
	padding: 0;
	width:100%;
	padding-left: 30px;
	font-size: 1.5vw;
    text-align: justify;
	
}

@media screen and (max-width: 854px) {
  .bio, .biotext, .bioimage{
  	margin: 0px 20px;
  	width: 90%;
  	flex-direction: column;
  	padding-left: 0px;
  	padding-top: 20px;
  	align-items: center;
  	font-size: 20px;
 }
}

@media screen and (max-width: 2000px) {
   .bioimage{	
  	width: 30%;
  	height: 20%;
 }
}

@media screen and (max-width: 854px) {
   .bioimage{	
  	width: 60%;
  	height: 20%;
 }
}
